@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;600&display=swap);
/* Import Poppins font */

/* Kleuren opgeslagen in variabelen */
:root {
  --main-color: #4528d5;
  --color-dark: #3a3a3a;
  --color-greyLight: #d8d7d7;
  --color-greyDark: #707070;
}

/* Wat we vanaf het begin willen toepassen denk aan margin reset font-family etc */
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style-type: none;
  text-decoration: none;
  font-family: "Poppins", sans-serif;
}

.center {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav {
  width: 82vw;
  height: 8vh;
  display: flex;
  float: right;
  justify-content: space-between;
  padding: 1rem 1.5rem;
  box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.2);
  background: #ffff;
  top: 0;
}

.nav__user__info {
  display: flex;
  align-items: center;
  padding-right: 95px;
}

.nav h2 {
  display: flex;
  align-items: center;
  font-weight: 400;
  font-size: 1.7rem;
  padding-left: 175px;
}

.nav__user__info h4 {
  font-size: 1.3rem;
  font-weight: 400;
}

.nav__user__info img {
  border-radius: 50%;
  margin-right: 1rem;
  height: 60px;
  width: 60px;
}

.sidebar {
  width: 18vw;
  position: fixed;
  height: 100%;
  left: 0;
  top: 0;
  background: var(--color-dark);
  z-index: 100;
  transition: width 300ms;
}

.sidebar__container {
  padding-left: 50px;
  padding-top: 40px;
}

.sidebar__brand {
  display: grid;
  height: 140px;
  padding: 1rem 0rem 1rem 2rem;
  color: #ffff;
}

.sidebar__brand h1 {
  margin-bottom: 30px;
}

.sidebar__brand p.time {
  padding-left: 2rem;
  font-size: 1.4rem;
}

.sidebar__brand p.date {
  margin-top: 0.5rem;
  padding-left: 1.3rem;
  font-size: 1.2rem;
}

.sidebar__menu li:first-child {
  margin-top: 3rem;
}

.sidebar__menu li {
  display: grid;
  align-content: center;
  width: 100%;
  margin-bottom: 2rem;
  padding-left: 2rem;
}

.sidebar__menu a {
  padding-left: 1rem;
  display: flex;
  color: #ffff;
  font-size: 1.4rem;
  align-items: center;
}

.sidebar__menu a.active {
  background: #ffff;
  padding-top: 1rem;
  padding-bottom: 1rem;
  color: var(--main-color);
  border-radius: 11px 0px 0px 11px;
}

.sidebar__menu a span:first-child {
  font-size: 1.5rem;
  padding-right: 1rem;
}

.sidebar__icon {
  font-size: 2rem;
  margin-right: 25px;
}

.lds-dual-ring {
  display: inline-block;
  width: 64px;
  height: 64px;
}

.lds-dual-ring:after {
  content: ' ';
  display: block;
  width: 46px;
  height: 46px;
  margin: 1px;
  border-radius: 50%;
  border: 5px solid #510077;
  border-color: #510077 transparent #510077 transparent;
  -webkit-animation: lds-dual-ring 1.2s linear infinite;
          animation: lds-dual-ring 1.2s linear infinite;
}

.loading-spinner__overlay {
  height: 100%;
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

@-webkit-keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes lds-dual-ring {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

